import { Component, OnInit } from '@angular/core';
import {DataserviceService} from '../dataservice.service'
import { registerdetails } from '../registerdetails';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  email:string;
  searchtext:String;
  teacher:boolean=false;
  student:boolean=false;
  status:string;

  constructor(private service: DataserviceService,private router :Router) { }

  ngOnInit() {
    this.email=localStorage.getItem('email');
    this.status=localStorage.getItem('status');


    if(this.status=="teacher"){
      this.teacher=true;
      
      
    }else{
      this.student=true;
     
    }
  }

  async getTeachers(){
    await this.service.getTeachers(this.email);
     this.router.navigate(['/teachers'])
   }
 
   async getStudents(){
     await this.service.getStudents(this.email)
      this.router.navigate(['/teachers'])
    }
    logout(){

      console.log(this.email+ " loging out")
      this.service.logout(this.email)
  
    }
    profile(){
      this.router.navigate(['/profile'])
  
    }
    async searchTeacher(){
      console.log(this.searchtext+" search text")
     await this.service.searchTeachers(this.searchtext);
     this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
  }
     this.router.onSameUrlNavigation = 'reload';
      this.router.navigate(['/search'])
  
    }
}
