import { Component, OnInit } from '@angular/core';
import {DataserviceService} from '../dataservice.service'
import { registerdetails } from '../registerdetails';
import { Router } from '@angular/router';
import { postdetails } from '../postdetails';
import { async } from '@angular/core/testing';
import { formatDate } from '@angular/common';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  name:string;
  email:string;
  status:string;
  subject:string;
  experience:string;
  city:string;
  searchtext:String;
  teacher:boolean=false;
  student:boolean=false;
  picstatus:boolean=true;
  user:registerdetails;
  statusText:string
  postheading:string
  postcontent:string
  profilepic:string
  url: string |ArrayBuffer="assets/maleteacher.jpg";

   posts:postdetails[];

  constructor(private service: DataserviceService,private router :Router) { }

 async ngOnInit() {
    
    
    
    console.log(localStorage.getItem('enteredemail'));
    this.name=localStorage.getItem('name');
    this.email=localStorage.getItem('email');
    this.status=localStorage.getItem('status');
    this.subject=localStorage.getItem('subject');
    this.experience=localStorage.getItem('experience');
    this.city=localStorage.getItem('city');
    this.profilepic=localStorage.getItem('profilepic');
    /*if(this.profilepic=="yes"){
      this.picstatus=false;
      this.url="http://localhost:8080/profilepic?id="+this.email
      

    }*/
    if(this.status=="teacher"){
      this.teacher=true;
      this.statusText="Students"
      await this.service.getTeacherPosts(this.email)
      this.posts = this.service.getpostResults().reverse();
      console.log()
    }else{
      this.student=true;
      await this.service.getStudentPosts(this.email)
      this.posts = this.service.getpostResults().reverse();
    }
    console.log(this.name);
    
  }

  
  fileToUpload: File = null;
  onSelectFile(event) { // called each time file input changes
    console.log(event);
    this.fileToUpload=event.target.files[0];
      if (event.target.files && event.target.files[0]) {
        var reader = new FileReader();

        reader.readAsDataURL(event.target.files[0]); // read file as data url

        reader.onload = (event) => { // called once readAsDataURL is completed
          this.url = reader.result;
        }
      }
  }

  onupload(){
    const fd = new FormData();
    fd.append("file",this.fileToUpload);
    fd.append("name",this.email);

    this.service.uploadFile(fd)
    this.ngOnInit()

  }

 async searchTeacher(){
    console.log(this.searchtext+" search text")
   await this.service.searchTeachers(this.searchtext);
    this.router.navigate(['/search'])

  }

  async getProfilePic(){
    await this.service.getprofilepic(this.email);
     
   }

 async getTeachers(){
   await this.service.getTeachers(this.email);
    this.router.navigate(['/teachers'])
  }

  async getStudents(){
    await this.service.getStudents(this.email)
     this.router.navigate(['/teachers'])
   }

  createPost(){
    console.log(this.postheading + this.postcontent)
    this.service.CreatePost(this.email,this.postheading,this.postcontent)
    this.ngOnInit()
    
  }

  profile(){
    this.router.navigate(['/profile'])

  }

  logout(){

    console.log(this.email+ " loging out")
    this.service.logout(this.email)

  }



}
