import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, interval, from } from 'rxjs';
import { catchError, retry, map } from 'rxjs/operators';
import { log } from 'util';
import {registerdetails} from './registerdetails';
import {loginDetails} from './logindeatils';
import { Router } from '@angular/router';
import { promise } from 'protractor';
import {searchDetails} from './searchdetails';
import {connectiondetails} from './connectiondetails'
import {postdetails} from './postdetails'


@Injectable({
  providedIn: 'root'
})
export class DataserviceService {

  user:registerdetails=new registerdetails("","","","","","","","","","");
  cdetails:connectiondetails;
  search:searchDetails;
  searchResults:registerdetails[];
  post:postdetails
  posts:postdetails[];
   objectURL:any;
  

  constructor(private http: HttpClient,private router :Router) { }



  public async  searchTeachers(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<registerdetails[]>('http://localhost:8080/searchTeacher?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.searchResults=res;

    })
     

  }

  public async  logout(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<registerdetails[]>('http://localhost:8080/logout?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.searchResults=res;

    })
     

  }

  public async  getTeachers(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<registerdetails[]>('http://localhost:8080/getTeachers?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.searchResults=res;

    })
     

  }

  public async  getprofilepic(t:String){
    
    console.log(t)

    await this.http.get('http://localhost:8080/profilepic?id='+t+'').subscribe((baseImage : any) => {
      //alert(JSON.stringify(data.image));
      this.objectURL = 'data:image/jpeg;base64,' + baseImage.image;

       console.log(this.objectURL +" image")
     
    })
     

  }
  public getpicurl(){
    return this.objectURL;
  }

  public async  getStudents(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<registerdetails[]>('http://localhost:8080/getStudents?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.searchResults=res;

    })
     

  }

  public async  getTeacherPosts(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<postdetails[]>('http://localhost:8080/getTeacherPosts?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.posts=res;

    })
    
     

  }

  public async  getStudentPosts(t:String){
    this.search= new searchDetails(t,"","");
    console.log(t)

    await this.http.get<postdetails[]>('http://localhost:8080/getStudentPosts?id='+t+'').toPromise().then(res=>{
      console.log(res);
      this.posts=res;

    })
    
     

  }

  

  public async  addConnection(semail:string,temail:string){

    console.log(semail+" student email")

    this.cdetails = new connectiondetails(semail,temail)
    

    await this.http.post('http://localhost:8080/addConnection',this.cdetails,{
      headers:new HttpHeaders(
        {
          'content-Type':'application/json',
          
     
        }
      )

    }).toPromise().then((data)=>{
      console.log(data);
     
    })
     

  }

  public async  uploadFile(file:FormData){
    console.log("After upload")
    localStorage.setItem('profilepic', "yes");

    await this.http.post('http://localhost:8080/uploadImage',file).toPromise().then((data)=>{
      console.log(data.toString()+" response data");
     
     
    })
    
   

     

  }

  public async  CreatePost(pemail:string,pheading:string,pcontent:string){

    console.log(pemail+" post email")

    this.post = new postdetails(pemail,pheading,pcontent);
    

    await this.http.post('http://localhost:8080/createPost',this.post,{
      headers:new HttpHeaders(
        {
          'content-Type':'application/json',
          
     
        }
      )

    }).toPromise().then((data)=>{
      console.log(data);
     
    })
     

  }

  public  getSearchResults():registerdetails[]{
    console.log(this.searchResults + "search results ")
    return this.searchResults 
    

  
  }

  public  getpostResults():postdetails[]{
    console.log(this.posts + "search results ")
    return this.posts 
    

  
  }

  

 



  public  createUser(user:registerdetails ){

    console.log(user.experience+" experience")
    this.http.post<registerdetails>('http://localhost:8080/postAddUser',user,{
      headers:new HttpHeaders(
        {
          'content-Type':'application/json',
          
     
        }
      )

    }).subscribe(data=>{
      console.log(data.status);
      
    })

   
  }

  public async  validateLogin(user:loginDetails ){

    //console.log(user.email+" email")

     await this.http.post<registerdetails>('http://localhost:8080/validatelogin',user,{
      headers:new HttpHeaders(
        {
          'content-Type':'application/json',
          
     
        }
      )

    }).toPromise().then((data)=>{
      console.log(data);
      
      this.user= new registerdetails(data.status,data.firstname,data.lastname,data.email,data.subject,data.experience,data.city,data.password,data.logged,data.profilepic)
      

    })

    console.log(this.user.email+" email")
    
  }

  public upload(formData) {
    
        return this.http.post<any>("", formData, {
    
          reportProgress: true,
   
          observe: 'events'
    
        });
    
    }

  
  

  public getUserDetails():registerdetails{
    console.log(this.user.firstname)
    localStorage.setItem('email', this.user.email);
    localStorage.setItem('name', this.user.firstname+" "+this.user.lastname);
    localStorage.setItem('status', this.user.status);
    localStorage.setItem('subject', this.user.subject);
    localStorage.setItem('experience', this.user.experience);
    localStorage.setItem('city', this.user.city);
    localStorage.setItem('profilepic', this.user.profilepic);

    return this.user;


  }
 
}
