import { Component, OnInit } from '@angular/core';
import {DataserviceService} from '../dataservice.service'
import { registerdetails } from '../registerdetails';
import { Router } from '@angular/router';

@Component({
  selector: 'app-getteachers',
  templateUrl: './getteachers.component.html',
  styleUrls: ['./getteachers.component.css']
})
export class GetteachersComponent implements OnInit {
  name:string;
  email:string;
  status:string;
  subject:string;
  experience:string;
  city:string;
  searchtext:String;
  teacher:boolean=false
  semail:string
  temail:string
  onlineStatus:string="offline"

  teacherresult:registerdetails[];

  constructor(private service: DataserviceService,private router :Router) { }

  ngOnInit() {

    this.teacherresult=this.service.getSearchResults();

  }

  profile(){
    this.router.navigate(['/profile'])

  }

}
