import { Component, OnInit } from '@angular/core';
import {registerdetails} from '../registerdetails';
import { Router } from '@angular/router';
import {DataserviceService} from '../dataservice.service'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {


  rUser:registerdetails;
  rbtn:boolean=false;
  emptystatus:boolean=false;
  emptypassword:boolean=false;
  poorpassword:boolean=false;
  emptyfirstname:boolean=false;
  emptylastname:boolean=false;
  invalidEmail:boolean=false;
  invalidatePassword:boolean=false;
  height:string="register-form";
  status:string="";
  
  firstname:string="";
  lastname:string="";
  email:string="";
  subject:string="";
  experience:string="";
  city:string="";
  description:string="";
  password:string="";
  repassword:string="";
  subjects:string[]=["java","selenium",".Net","python","Maths 10th","Maths 12th","Maths UG","Science 10th","Science 12th","Science UG","Banking Exams","Computer Science","Electrican Engineering","Electronins"];

  constructor(private service: DataserviceService, private router :Router) { }

  ngOnInit() {
  }

  validateFields(){
    if(!((this.email.includes("@"))&&(this.email.includes(".com")))){
      
      this.invalidEmail=true;
      console.log("invalid email"+this.invalidEmail);
      
    }else{
      this.invalidEmail=false;
    }
    console.log("invalid email"+this.invalidEmail);
    
    if((this.password=="")||!(this.password==this.repassword)){
     
      this.invalidatePassword=true;
      console.log("Invalid password"+this.invalidatePassword)
      this.height="register-form-teacher";
    }else{
      this.invalidatePassword=false;
    }
    console.log("Invalid password"+this.invalidatePassword)
    if(this.password.length<=6){
      this.poorpassword=true;
    }else{
      this.poorpassword=false;
    }
    if(this.firstname==""){
      this.emptyfirstname=true;
      console.log("empty first name"+this.emptyfirstname)

    }else{
      this.emptyfirstname=false;
    }
    console.log("empty first name"+this.emptyfirstname)
    if(this.lastname==""){
      this.emptylastname=true;
      console.log("empty last name"+this.emptylastname)
    }else{
      this.emptylastname=false;
    }
    console.log("empty last name"+this.emptylastname)
    
    if(this.status==""){
      this.emptystatus=true;
      console.log("empty status"+this.emptystatus)
    }else{
      this.emptystatus=false;
    }
    console.log("empty status"+this.emptystatus)
    if(this.password==""){
      this.emptypassword=true;
      
      console.log("empty password"+this.emptypassword)
    }else{
      this.emptypassword=false;
    }
    console.log("empty password"+this.emptypassword)
    console.log("Fields validated")
    

  } 

  

  addTeacherFields(){
    
      this.rbtn=true;
      this.height="register-form-teacher";
    
    
  }
  addStudentsFields(){
    this.rbtn=false;
     // this.height="register-form";
  }
  onRegister(){
    this.validateFields();
    
    console.log(this.status+this.firstname+this.lastname+this.email+this.subject+this.experience+this.city+this.password+this.repassword);
    this.sendregisterRequest();
  }

  sendregisterRequest(){
    
    if(this.invalidEmail||this.invalidatePassword||this.emptyfirstname||this.emptylastname||this.emptystatus||this.emptypassword||this.poorpassword){
      console.log("fields are missing")
      this.height="register-form-teacher";
          }else{
      
          console.log("request sent")
          this.rUser= new registerdetails(this.status,this.firstname,this.lastname,this.email,this.subject,this.experience,this.city,this.password,"offline","no");
          console.log(this.rUser.password);
         
      
          this.service.createUser(this.rUser);
          this.router.navigate([''])
         
        }

  }

}
