import { Component, OnInit } from '@angular/core';
import {DataserviceService} from '../dataservice.service'
import { registerdetails } from '../registerdetails';
import { Router } from '@angular/router';
import { Console } from 'console';
import {connectiondetails} from '../connectiondetails'

@Component({
  selector: 'app-searchpage',
  templateUrl: './searchpage.component.html',
  styleUrls: ['./searchpage.component.css']
})
export class SearchpageComponent implements OnInit {

  name:string;
  email:string;
  status:string;
  subject:string;
  experience:string;
  city:string;
  searchtext:String;
  teacher:boolean=true
  semail:string
  temail:string

  searchresult:registerdetails[];
  
  

  constructor(private service: DataserviceService,private router :Router) { }

  ngOnInit() {
    this.searchresult= this.service.getSearchResults();
   
  }

  onConnect(email:string){
    console.log(email + " connect name")
    this.semail= localStorage.getItem('email');
    this.temail= email;
    this.service.addConnection(this.semail,this.temail)
    
   
    

  }
  profile(){
    this.router.navigate(['/profile'])

  }

}
