import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {loginDetails} from '../logindeatils';
import {DataserviceService} from '../dataservice.service'
import { registerdetails } from '../registerdetails';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:string="";
  password:string="";
  invalidEmail:boolean=false;
  invalidpassword:boolean=false;


  lUser:loginDetails;
  rUser : registerdetails;


  constructor(private service: DataserviceService,private router :Router) { }

  ngOnInit() {
    
    
  }

  validatefields(){

    if(!(this.email.includes("@")&&this.email.includes(".com"))){
      this.invalidEmail=true;
    }else{
      this.invalidEmail=false;
    }
    console.log("Invalid email"+this.invalidEmail)

  }

  register(){
    console.log("clicked register")
    this.router.navigate(['/register'])
  }

   async loginSubmit(){
    this.validatefields();
    console.log(this.email+this.password);
   

    this.lUser = new loginDetails(this.email,this.password);
    await this.service.validateLogin(this.lUser);
   this.rUser= this.service.getUserDetails();
   if(this.rUser.email.length==0){
    this.invalidEmail=true;

   }
    if(!(this.invalidEmail||this.invalidpassword))
    this.router.navigate(['/profile']);
    console.log(this.rUser)
   
  }

}
